* {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  font-size: 17px;
  font-weight: 500;
  letter-spacing: 0.02em;
  transition: all ease-in-out 0.2s;
}

html, body, main {
  min-width: 300px;
}

.f-c {
  display: flex;
  align-items: center;
  justify-content: center;
}

body {
  width: 100%;
  height: 100%;
}

main {
  max-width: 1200px;
  width: 80%;
  height: 1000px;
}

button {
  padding: 8px 20px;
  border-radius: 5px;
  border: none;
  box-shadow: 2px 2px 5px #96969669;
}

button:hover { cursor: pointer; }

button:focus { outline: none; }

.green-btn {
  background-color: #5fcc63;
  color: #404040;
}
.green-btn:hover { background-color: #6ae76e; }

.red-btn {
  background-color: #f15050;
  color: #404040;
}
.red-btn:hover { background-color: #f56c6c; }

@media(max-width: 650px) {
  #onetime-code, #delivery-name {width: 150px;}
  #delivery-type, #onetime-code {width: 240px;}
  #onetime-code {margin-bottom: 10px;}
  #delivery-name {
      margin-right: 0;
      margin-bottom: 10px;
      width: 250px;
  }
  .input-wrap {
      display: flex;
      flex-wrap: wrap;
  }
  .connected {width: 300px;}
  .code-block { height: max-content;}
}

@media(max-width: 768px) {
  main { width: 100%; }
}